*,
*:before,
*:after {
    box-sizing: border-box;
}

img {
    max-width: 100%;
    height: auto;
    vertical-align: middle;
}

.header {
    background-color: #aa0601;
    color: white;
    padding: 30px 88px;
    display: flex;
    justify-content: space-between;
}

@media(max-width:768px) {
    .header {
        padding: 30px 39px;
    }
}

@media(max-width:767px) {
    .header {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 10px 0 0 0;
    }

}


.header-menu {
    column-gap: 64px;
    display: flex;
    flex-wrap: wrap;
}

@media(max-width:768px) {
    .header-menu {
        column-gap: 40px;
    }
}

@media(max-width:767px) {
    .header-menu {
        width: 100%;
        column-gap: 0px;
        text-align: center;
    }
}


.header-menu a {
    text-decoration: none;
    color: white;
}

.menu-item {
    font-size: 20px;
    line-height: 1.5;
}

@media(max-width:767px) {
    .menu-item {
        width: 50%;
        border: 1px solid white;
    }
}

.logo {
    width: 80px;
    height: 48px;
}

@media(max-width:767px) {
    .logo {
        width: 48px;
        height: 29px;
        margin-bottom: 10px;
    }
}

.logo a {
    background: url(../images/logo.png)no-repeat center;
    display: block;
    width: 80px;
    height: 48px;
    text-indent: 101%;
    overflow: hidden;
    white-space: nowrap;
    background-size: contain;
}

@media(max-width:767px) {
    .logo a {
        width: 48px;
        height: 29px;
    }
}

.footer {
    background-color: #aa0601;
}

.footer-menu {
    font-size: 16px;
    line-height: 1.5;
}

footer li a {
    text-decoration: none;
    color: white;
}

.menu-contact {
    padding-top: 44px;
}

@media(max-width:767px) {
    .menu-contact {
        display: none;
    }
}

.menu-list {
    padding-right: 48px;
}

@media(max-width:992px) {
    .menu-list {
        padding-right: 40px;
    }
}

.media-icon {
    padding-top: 32px;
}

@media(max-width:767px) {
    .media-icon {
        padding-top: 24px;
    }
}

.media-icon li {
    padding-right: 32px;
}

@media(max-width:768px) {
    .media-icon li {
        padding-right: 24px;
    }
}

.contact {
    font-size: 36px;
    line-height: 1.5;
}

@media(max-width:992px) {
    .contact {
        font-size: 20px;
        line-height: 1.5;
    }
}

@media(max-width:767px) {
    .contact {
        font-size: 16px;
        line-height: 1.875;
    }
}

@media(max-width:767px) {
    .contact {
        padding-bottom: 25px;
    }
}

.contact-tele li {
    padding-left: 20px;
}

.tele {
    width: 36px;
    height: 36px;
}

@media(max-width:992px) {
    .tele {
        width: 24px;
        height: 24px;
    }
}

.contact-email li {
    padding-left: 20px;
}

.email {
    width: 40px;
    height: 32px;
}

@media(max-width:992px) {
    .email {
        width: 24px;
        height: 24px;
    }
}

.contact-tele {
    padding-top: 36px;
    padding-bottom: 16px;

}

.contact-email {
    padding-bottom: 24px;
    border-bottom: 1px solid white;
}

.private {
    font-size: 16px;
    line-height: 1.5;
    color: white;
    padding-top: 32px;
    padding-bottom: 39px;
}

.private-list1 {
    padding-right: 38.375rem;
}